const ToggleButtonsMultiple = () => {
  const [item, setItem] = useState('other');

  const handleItem = (event: React.MouseEvent<HTMLElement>, newItem: string | null) => {
    if (newItem !== null) {
      setItem(newItem);
    }
  };

  return (
    <ToggleButtonGroup value={item} onChange={handleItem} exclusive aria-label="text formatting">
      <ToggleButton value="atom" aria-label="bold">
        <StopIcon />
      </ToggleButton>
      <ToggleButton value="other" aria-label="italic">
        <SearchIcon />
      </ToggleButton>
    </ToggleButtonGroup>
  );
}

const ComboBox = () => {
  return (
    <Box display="flex">
      <IconButton>
        <RemoveCircleIcon />
      </IconButton>
      <ToggleButtonsMultiple />
      <Autocomplete
        options={top100Films}
        getOptionLabel={(option) => option.title}
        style={{ width: 300 }}
        renderInput={(params) => <TextField {...params} label="Combo box" variant="outlined" />}
      />
      <IconButton>
        <EditIcon />
      </IconButton>
    </Box>
  );
}

