const StructureView = () => {
  const [structureTag, setStructureTag] = useState<StructureTag>('or');

  const ignoreLabelClick = (event: MouseEvent<Element>) => {
    event.preventDefault();
  }

  const ignoreSelect = (event: ChangeEvent<{}>, nodeIds: string[]) => {
    event.preventDefault();
  }

  return (
    <Box display="flex" flexDirection="column">
      <TreeView
        defaultExpanded={['1']}
        defaultCollapseIcon={<ExpandMoreIcon />}
        defaultExpandIcon={<ChevronRightIcon />}
        selected={[]}
        onNodeSelect={ignoreSelect}
      >
        <TreeItem nodeId="1" onLabelClick={ignoreLabelClick} label={
          <StructureTagView
            structureTag={structureTag}
            setStructureTag={setStructureTag}
          />
        }>
          <TreeItem nodeId="2" onLabelClick={ignoreLabelClick} label={
            <Box marginTop={1} marginBottom={1}>
              <ComboBox />
            </Box>
          }
          />
          <TreeItem nodeId="3" onLabelClick={ignoreLabelClick} label={
            <Box marginTop={1} marginBottom={1}>
              <ComboBox />
            </Box>
          }
          />
          <TreeItem nodeId="1-add" onLabelClick={ignoreLabelClick} label={
            <IconButton>
              <AddCircleIcon />
            </IconButton>
          }
          />
        </TreeItem>
      </TreeView>
    </Box>
  );
}

export default StructureView;
